/* ~~~~~~~~~~ Body ~~~~~~~~~~ */
body {
  font-weight: 300;
  font-family: sofia-pro, sans-serif;
  font-style: normal;
  font-size: 18px;
  line-height: 1.3;
  background: #fff;
  color: #54565a; }
