/* ~~~~~~~~~~ To top ~~~~~~~~~~ */
.return-to-top {
  position: fixed;
  bottom: 50px;
  right: 50px;
  background: #aaa;
  width: 45px;
  height: 45px;
  text-decoration: none;
  opacity: 0;
  visibility: hidden;
  z-index: 5;
  border-radius: 50%;
  text-align: center;
  color: #fff; }
  @media (max-width: 991.98px) {
    .return-to-top {
      display: none !important; } }
  .return-to-top--visible {
    opacity: 0.75;
    visibility: visible; }
    .return-to-top--visible:hover {
      opacity: 1; }
  .return-to-top i {
    font-size: 15px;
    line-height: 1.3;
    position: relative;
    top: -2px; }
    @media (min-width: 576px) {
      .return-to-top i {
        font-size: calc(15px + 1 * ((100vw - 576px) / 624)); } }
    @media (min-width: 1200px) {
      .return-to-top i {
        font-size: 16px; } }

